import BaseApiService from "../../BaseApiService";

// ---------------- Default path ------------------ //
const DEFAULT_PATH = "/custom/api/deposit";

class DepositAccountService extends BaseApiService {
  getBankAccount = (start = 0, limit = 10, name: string) => {
    const endPoint =
      DEFAULT_PATH +
      `/account/search?isActive=true&bank.name:lk=${name}&start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  getBankDetails = (start = 0, limit = 10, search: string) => {
    const endPoint =
      DEFAULT_PATH +
      `/account/search?isActive=true&start=${start}&limit=${limit}&${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}
export default new DepositAccountService();
